      margin-top: 10px;
      background: transparent;
      border-radius: 10px;
      -webkit-transition: all ease-in-out 0.5s;
      transition: all ease-in-out 0.5s;
    }

    /* Style the scrollbar on hover */
    ::-webkit-scrollbar-thumb:hover {
      background: transparent;
    }

    /* Set the width of the scrollbar */
